import request from '../utils/request'

/**
 * 获取商品列表
 * @param {String} page 分页
 * @returns 
 */

export function getShopList(page){
    // 注意：执行getShopList时候，返回的是promise
    // promise有：
    // 1：promiseStatus：记录任务状态
    // 2：promiseValue：存放响应式数据
   return request({
        url:'/api/goodList',
        method:'get',
        params:{
            page
        }
    })
}

export function getTypeOne(){
    return request({
        url: '/api/getTypeOne',
        method: 'get'
    })
}