<template>
  <div class="shoplist">
    <template v-for="(item, index) in TypeTwoList">
      <shop @onShopClick="linkDetail" :key="index" :shop="item" />
    </template>
  </div>
</template>

<script>
import { getTypeTwoList } from "../../../api/sorting.js";
export default {
  name: "ShopList",
  props: {
    type_two: String,
    type_one: String,
  },
  data() {
    return {
      TypeTwoList: [],
    };
  },

  methods: {
    getTypeTwoList() {
      let data = { type_two: this.type_two, type_one: this.type_one };
      // console.log(data);
      getTypeTwoList(data).then((data) => {
        this.TypeTwoList = data;
      });
    },
    // 跳转详情页
    linkDetail(item) {
      this.$router.push(`/detail/${item.Id}?type_one=${item.type_one}`);
    },
  },
  created() {
    this.getTypeTwoList();
  },
  watch: {
    type_two: {
      handler() {
        this.getTypeTwoList();
      },
    },
  },
};
</script>

<style lang='less' scoped>
.shoplist {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
</style>