<template>
  <div class="sorting">
    <!-- <LeftNav :typeTwoList="typeTwoList" /> -->
    <div class="">
      <top-category :typeOneList="typeOneList"/>
    </div>
    <div class="leftNav">
      <div
        @click="setFloorNavMountClick(index)"
        v-for="(item, index) in typeTwoList"
        :key="index"
        :class="{ nav_item: true, 'item-bg': index == clickItem }">
        {{ item }}
      </div>
    </div>
    <div class="shop-content">
      <div class="floor_item" v-for="(item, index) in typeTwoList" :key="index">
        <h1>{{ item }}</h1>
        <shop-list :type_two="item" :type_one="typeOne" />
      </div>
    </div>
    <back-top />
  </div>
</template>

<script>
import { getTypeTwo } from "../../api/sorting.js";
import { getTypeOne } from "../../api/home.js";
import LeftNav from "./components/LeftNav.vue";
import ShopList from "./components/Shoplist.vue";
import TopCategory from "../../components/TopCategory.vue";
export default {
  name: "Sorting",
  components: { LeftNav, ShopList, TopCategory },
  data() {
    return {
      typeTwoList: [],
      typeOneList: [],
      typeOne: this.$route.query.type_one,
      element: {
        nav_item: null,
        floor_item: null,
      }, // 存储页面dom元素
      timer: null,
      clickItem: 0, // 记录当前点击的侧边栏
    };
  },
  watch:{
    $route:{
      deep:true,
      handler(){
        this.getTypeTwo();
        this.typeOne = this.$route.query.type_one;
      }
    }
  },
  methods: {
    // 获取type_two列表
    getTypeTwo() {
      let type_one = this.$route.query.type_one;
      getTypeTwo(type_one).then((data) => {
        this.typeTwoList = data;
      });
    },
    getTypeOne() {
      getTypeOne().then((data) => {
        this.typeOneList = data;
      });
    },
    /**
     * 给导航栏 item 编写事件处理函数
     */
    setFloorNavMountClick(index) {
      this.clickItem = index;
      const { floor_item } = this.element;
      // 获取楼层盒子距离顶部的高度
      const floor_offSetTop =
        floor_item[index].offsetTop - floor_item[0].offsetTop;
      // 获取鼠标滚动距离
      const window_scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // timer
      const timer = {
        step: 60,
        timer: 16,
        FLOOR_OFFSETTOP: floor_offSetTop,
      };
      if (window_scrollTop > floor_offSetTop) {
        this.setFloorSCrollArrowUp(timer);
      } else if (window_scrollTop == floor_offSetTop) {
        return false;
      } else {
        this.setFloorScrollArrowDown(timer);
      }
    },
    /**
     *  设置楼层向上滚动  timer 定时器配置
     */
    setFloorSCrollArrowUp(timer) {
      // 清除原有定时器
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        // 获取鼠标滚动位置
        let window_scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (window_scrollTop <= timer.FLOOR_OFFSETTOP) {
          document.documentElement.scrollTop = timer.FLOOR_OFFSETTOP;
          document.body.scrollTop = timer.FLOOR_OFFSETTOP;
          clearInterval(this.timer);
        } else {
          document.documentElement.scrollTop = window_scrollTop - timer.step;
          document.body.scrollTop = window_scrollTop - timer.step;
        }
      }, timer.times);
    },
    /**
     * 设置楼层向下滚动
     */
    setFloorScrollArrowDown(timer) {
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        let window_scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (window_scrollTop + 500 >= timer.FLOOR_OFFSETTOP) {
          document.documentElement.scrollTop = timer.FLOOR_OFFSETTOP;
          document.body.scrollTop = timer.FLOOR_OFFSETTOP;
          clearInterval(this.timer);
        } else {
          document.documentElement.scrollTop = window_scrollTop + timer.step;
          document.body.scrollTop = window_scrollTop - timer.step;
        }
      }, timer.times);
    },
    /**
     * 监听窗口滚动楼层导航动态定位
     */
    floorSrcollEventListener() {
      const { nav_item, floor_item } = this.element;
      const window_scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      for (let i = 0, len = floor_item.length; i < len; i++) {
        let floor_offsetTop = floor_item[i].offsetTop - floor_item[0].offsetTop;
        if (window_scrollTop >= floor_offsetTop - 120) {
          this.clickItem = i;
        }
      }
    },
  },
  created() {
    this.getTypeTwo();
    this.getTypeOne();
  },
  mounted() {
    // 获取dom元素
    this.element = {
      nav_item: document.getElementsByClassName("nav_item"), //获取侧边导航栏dom元素
      floor_item: document.getElementsByClassName("floor_item"), // 获取中间楼层dom元素
    };

    // this.element.nav_item[0].classList.add("item-bg");
    window.addEventListener("scroll", this.floorSrcollEventListener);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.floorSrcollEventListener); //销毁个滚动事件 
  },

};
</script>

<style lang='less' scoped>
.sorting {
  width: 1200px;
  margin: 20px auto;
}
.shop-content {
  width: 100%;
  margin: 20px 0px;
}
.item-bg {
  font-weight: bolder;
  color: purple;
  filter: drop-shadow(purple 0 0 10px);
}
.leftNav {
  position: fixed;
  left: 10px;
  top: 300px;
  width: 130px;
  border: 1px solid #9c689c;
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  background-color: #fff;
  padding: 15px 0px;
  .nav_item {
    margin: 0;
    height: 2.2em;
    line-height: 2.2em;
    font-size: 16px;
    cursor: pointer;
    user-select: none;
    &:hover {
      font-weight: bolder;
      color: purple;
      filter: drop-shadow(purple 0 0 10px);
    }
  }
}
</style>