import request from '../utils/request.js'

// 获取商品分类页数据
export function getTypeTwo(type_one){

    return request({
        url: '/api/getTypeTwo',
        method: 'get',
        params: {
            type_one
        }
    })

}

// 获取二级分类数据
export function getTypeTwoList(data){
    return request({
        url: '/api/getTypeTwoList',
        method: 'get',
        params: {
            ...data
        }
    })
}