<template>
  <div class="typeone">
    <div
      @click="onClickHandler(item)"
      v-for="(item, index) in typeOneList"
      :key="index"
    >
      <router-link
        :class="{ 'router-active': item == typeOne }"
        :to="'/sorting?type_one=' + item"
        >{{ item }}</router-link
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "TopCategory",
  props: ["typeOneList"],

  data() {
    return {
      typeOne: this.$route.query.type_one,
    };
  },
  methods: {
    onClickHandler(item) {
      this.$emit("onClick", item);
    },
  },
  updated() {},
  watch: {
    $route: {
      deep: true,
      handler() {
        this.typeOne = this.$route.query.type_one;
      },
    },
  },
};
</script>

<style lang='less' scoped>
.typeone {
  padding: 20px 0px 10px;
  display: flex;
  justify-content: space-between;

  a {
    cursor: pointer;
    width: 100%;
    margin: 0px 40px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
    &:hover {
      background-color: #800080;
      color: #fff;
      border-radius: 20px;
    }
  }
}
.router-active {
  background-color: #800080;
  color: #fff;
  border-radius: 20px;
}
</style>